$primary-color: #368184;
$secondary-color: #626261;
$font-stack: Helvetica, sans-serif;

body,
h1,
h2,
p {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: $font-stack;
  line-height: 1.6;
  background-color: #f4f4f4;
}

header {
  text-align: center;
  padding: 24px;
  color: #fff;
  background-color: $primary-color;
}

main {
  padding: 24px;
  margin-top: 24px;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.1);
  background-color: #fff;
}

footer {
  text-align: center;
  padding: 8px;
  margin-top: 24px;
  color: #fff;
  background-color: $primary-color;
}

h2 {
  color: #333;
  margin-bottom: 8px;
}

nav {
  margin: 24px 0;

  ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: $primary-color;

    li {
      float: left;

      a {
        display: block;
        color: white;
        text-align: center;
        padding: 8px 16px;
        text-decoration: none;

        &:hover:not(.active) {
          background-color: $secondary-color;
        }
      }
    }
  }
}

.container {
  width: 80%;
  margin: auto;
  overflow: hidden;
}

.profile-picture {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  margin: 24px auto;
  background-color: $secondary-color;
  object-fit: cover;
}

.active {
  background-color: $secondary-color;
}

.friend-container {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  justify-content: center;
  margin-top: 20px;
}

.friend-card {
  border: 1px solid #ccc;
  padding: 10px 15px;
  border-radius: 5px;
  background-color: #f2f2f2;
  font-size: 14px;
  min-width: 140px;
  text-align: center;
}